.caption {
  padding: 12px 24px 16px;
  box-shadow: inset 0px -1px 0px #f0f0f0;
}

.caption-filling {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.save {
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
}

.button-full {
  font-size: 14px !important;
  font-weight: 600;
  cursor: pointer;
}

.stream-btn {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background: url("@/assets/icons/stream-demonstration.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: fixed;
  z-index: 100;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
}

@media (max-width: 700px) {
  .caption {
    padding: 12px 10px 16px 10px;
  }

  .stream-btn {
    width: 40px;
    height: 40px;
    background: url("@/assets/icons/stream-demonstration.svg");
    background-size: contain;
    background-repeat: no-repeat;
    right: 16px;
    bottom: 16px;
  }
}

.buttons-container {
  display: flex;
  gap: 29px;
}
</style>
